<template>
	<div :class="['input-color-wrapper', block && '--block']">
		<label v-if="label" :for="uid" class="mb-2">{{ label }}</label>
		<div>
			<div
				v-loading="loading"
				@click="handleClick"
				@focus="handleClick"
				tabindex="0"
				:class="['input-color', disabled && '--disabled']">
				<div class="pr-4">
					<i
						class="input-color__selector material-icons"
						:style="{ color: value }">
						water_drop
					</i>
				</div>
				<div class="flex-grow">{{ selectedColor }}</div>
				<div class="pl-4">
					<i class="material-icons icon-sm text-grey-03">palette</i>
				</div>
			</div>
			<input
				v-model="value"
				@change="handleChange"
				@input="handleInput"
				:id="uid"
				class="w-0 h-0 opacity-0"
				type="color" />
		</div>
	</div>
</template>
<script>
import { computed, defineComponent, ref, watch } from 'vue'
export default defineComponent({
	name: 'Colorpicker',
	props: {
		modelValue: {
			type: String,
			default: '#000000'
		},
		block: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		loading: {
			type: Boolean,
			default: false
		},
		label: {
			type: String,
			default: 'Color'
		}
	},
	emits: ['update:modelValue', 'change', 'input'],
	setup(props, { emit }) {
		const uid = Math.floor(Math.random() * 1000000)
		const value = ref(props.modelValue)
		const selectedColor = computed(() => value.value?.toUpperCase())
		const handleClick = () => {
			if (props.disabled) return null
			document.getElementById(uid).click()
		}
		const handleChange = (e) => {
			emit('update:modelValue', e.target.value)
			emit('change', e.target.value)
		}
		const handleInput = (e) => {
			emit('input', e.target.value)
		}
		watch(
			() => props.modelValue,
			(newValue) => {
				value.value = newValue
			}
		)
		return {
			uid,
			value,
			selectedColor,
			handleClick,
			handleChange,
			handleInput
		}
	}
})
</script>
<style lang="scss">
.input-color-wrapper {
	@apply block w-max;
	&.--block {
		@apply w-full;
	}
}
.input-color {
	@apply transition duration-300 flex w-full flex-row items-center cursor-pointer border border-grey-01 px-4 py-2 box-border rounded-lg focus:border-teal-04;
	&__selector {
		@apply text-grey-04;
	}
	&.--disabled {
		@apply bg-grey-01 focus:border-grey-01;
	}
}
</style>
